:root {
  --black: #0D0D0D;
  --lightest-grey: #E6E6E6;
  --light-grey: #D5D5D5;
  --grey: #939393;
  --dark-grey: rgb(121, 121, 121);
  --accent: #FF6AEE;
  --light-blue: #26C6C6;
}

body {
  position: fixed;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
  top: 40px;
  left: 40px;
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
  padding: 0;
  font-family: "Roboto", "Helvetica Neue" sans-serif; 
}

.piano-roll {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 100%;
  /* height: 80vh; */
  /* min-height: 650px; */
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.piano-roll-editor {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column-reverse nowrap;
          flex-flow: column-reverse nowrap;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.key-row {
  height: 20px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-align-items: center;
          align-items: center;
}

.key-row.black {
  background-color: #939393;
  background-color: var(--grey);
}

.key-row.white {
  background-color: #E6E6E6;
  background-color: var(--lightest-grey);
}
.key-row.white:nth-of-type(odd) {
  background-color: #D5D5D5;
  background-color: var(--light-grey);
}

.steps-row {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.piano-cell {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-right: 1px solid rgb(121, 121, 121);
  border-right: 1px solid var(--dark-grey);
  font-family: 'Roboto';
  font-size: 10px;
}

.piano-cell.active {
  background-color: #26C6C6;
  background-color: var(--light-blue);
  border-radius: 5px;
  border-color: #26C6C6;
  border-color: var(--light-blue);
}

.piano-cell p::after {
  content: '%';
}

/* KEYS */

.piano-roll-keys {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column-reverse nowrap;
          flex-flow: column-reverse nowrap;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.piano-key {
  height: 20px;
  width: 40px;
  padding: 0px;;
  margin: 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
}

.piano-key.black {
  background-color: #0D0D0D;
  background-color: var(--black);
  color: #D5D5D5;
  color: var(--light-grey);
}

.piano-key.white {
  background-color: #D5D5D5;
  background-color: var(--light-grey);
  color: #0D0D0D;
  color: var(--black);
}
.piano-key p {
  font-family: 'Roboto';
  font-size: 10px;
  padding: 0;
  margin: 0;
}
